import React from "react";

export default function Counter() {
  return (
    <>
      <section id="counter">
        <div className="layer1 d-flex align-items-center justify-content-center">
          <div className="row m-5">
            <div className="col-lg-3 col-md-6">
              <div className="card px-3 py-5 text-center">
                <div className="counter-ico">
                  <i class="fa fa-check"></i>
                </div>
                <h3 className="text-white">450</h3>
                <p className="text-white">WORKS COMPLETED</p>
              </div>
            </div>

            <div className="col-lg-3 col-md-6">
              <div className="card px-3 py-5 text-center">
                <div className="counter-ico">
                  <i class="fa-solid fa-book-journal-whills"></i>
                </div>
                <h3 className="text-white">25</h3>
                <p className="text-white">YEARS OF EXPERIENCE</p>
              </div>
            </div>

            <div className="col-lg-3 col-md-6">
              <div className="card px-3 py-5 text-center">
                <div className="counter-ico">
                  <i class="fa-solid fa-user-group"></i>
                </div>
                <h3 className="text-white">550</h3>
                <p className="text-white">TOTAL CLIENTS</p>
              </div>
            </div>

            <div className="col-lg-3 col-md-6">
              <div className="card px-1 py-5 text-center">
                <div className="counter-ico">
                  <i class="fa-solid fa-award"></i>
                </div>
                <h3 className="text-white">48</h3>
                <p className="text-white">AWARD WON</p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
