import React from 'react';
// import { Link } from 'react-router-dom';

export default function Navbar() {

    function handleLink(num){
        let navLinks = document.querySelectorAll(".nav-item")
        console.log(navLinks);
        navLinks.forEach((nav)=>{nav.classList.remove("active")})
        navLinks[num].classList.add("active")
    }

    return (
        <>
            <nav className="navbar navbar-expand-lg navbar-dark  fixed-top">
                <div className="container">
                    <a className="navbar-brand fw-bold" href="#home">DevFolio</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                            <li onClick={()=>{handleLink(0)}} className="nav-item active">
                                <a className="nav-link " aria-current="page" href="#home">Home</a>
                                <div class="line"></div>
                            </li>
                            <li onClick={()=>{handleLink(1)}} className="nav-item">
                                <a className="nav-link" href="#about">About</a>
                                <div class="line"></div>
                            </li>
                            <li onClick={()=>{handleLink(2)}} className="nav-item">
                                <a className="nav-link" href="#services">Services</a>
                                <div class="line"></div>
                            </li>
                            <li onClick={()=>{handleLink(3)}} className="nav-item">
                                <a className="nav-link" href="#work">Work</a>
                                <div class="line"></div>
                            </li>
                            <li onClick={()=>{handleLink(4)}} className="nav-item">
                                <a className="nav-link" href="#blog">Blog</a>
                                <div class="line"></div>
                            </li>
                            <li onClick={()=>{handleLink(5)}} className="nav-item">
                                <a className="nav-link" href="#contact">Contact</a>
                                <div class="line"></div>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </>
    )
}
